import { useEffect, useState } from 'react';
import {
  Container,
  Icon,
  Menu,
  Sidebar,
  Header,
  Message,
  Modal,
  Button
} from 'semantic-ui-react'
import './App.css';
import 'semantic-ui-css/semantic.min.css'

const BASE_URL = (accessToken) => `https://chmkms-demo.herokuapp.com/api/live/?accessToken=${accessToken}&userAgent=${navigator.userAgent}`

const LOGOUT = (accessToken) => `https://chmkms-demo.herokuapp.com/api/live/logout?accessToken=${accessToken}`

const LIST_URL = (accessToken, standard) => `http://chmkms-demo.herokuapp.com/api/class/${standard}/recorded-videos?accessToken=${accessToken}`

async function loadLiveData(accessToken, cb) {
  try {
    const response = await fetch(BASE_URL(accessToken))
    const json = await response.json()
    cb.map(callback => callback(json.data))
  } catch (error) {
    console.error(error)
  }
}

function formatVideoArray(videos) {
  if (videos && videos.length) {
    return videos.reduce((acc, currentVid) => ({
      ...acc,
      [currentVid.subject]: acc[currentVid.subject] ? [...acc[currentVid.subject], currentVid] : [currentVid]
    }), [])
  }
  else return []
}

async function loadRecordedVideos(accessToken, standard, updateRecordedVideoList) {
  try {
    const response = await fetch(LIST_URL(accessToken, standard))
    const json = await response.json()
    updateRecordedVideoList(formatVideoArray(json.data))
  } catch (error) {
    console.error(error)
  }
}

const ELIGIBILITY_STATES = {
  PAISA_DIYA: 1,
  PAISA_NAHI_DIYA: -1
}

function App() {
  const [accessToken, updateAccessToken] = useState()
  const [apiData, updateApiData] = useState({})
  const [visible, updateVisible] = useState(false)
  const [vidList, updateVidList] = useState(null)
  const [liveData, updateLiveData] = useState(null)
  const [isEligibility, updateEligibility] = useState(true)
  const [showLogout, updateLogOutModal] = useState(false)

  useEffect(() => {
    const params = new URLSearchParams(window.location.search)
    const accessTokenURL = params.get('accessToken')
    if (accessTokenURL) updateAccessToken(accessTokenURL)
    else if(localStorage.getItem('accessToken')) updateAccessToken(localStorage.getItem('accessToken'))
    else updateAccessToken(window.prompt('Enter Access Token'))
  }, [])

  useEffect(() => {
    if(accessToken){
      localStorage.setItem('accessToken', accessToken)
      loadLiveData(accessToken, [updateApiData, updateLiveData])
    }
    }, [accessToken])
  const { 
    standard, 
    subject, 
    eligibility, 
    message, 
    name, 
    userAgent, 
    videoUrl,
    meetingLink
  } = apiData

  useEffect(() => {
    if(standard || subject) document.title = `CHMKMS | ${standard} | ${subject}`
  }, [standard, subject])

  useEffect(() => {
    if (ELIGIBILITY_STATES.PAISA_DIYA === eligibility) {
      if (userAgent === navigator.userAgent) return
      updateEligibility(false)
    } else if (ELIGIBILITY_STATES.PAISA_NAHI_DIYA === eligibility) updateEligibility(false)
  }, [userAgent, isEligibility, eligibility])

  useEffect(() => {
    if (standard && accessToken && isEligibility) loadRecordedVideos(accessToken, standard, updateVidList)
  }, [standard, accessToken, isEligibility])

  const showMenu = () => updateVisible(true)
  const hideMenu = () => updateVisible(false)

  const showLogoutMenu = () => updateLogOutModal(true)
  const hideLogoutMenu = () => updateLogOutModal(false)

  function updateCurrentVideo(video) {
    updateApiData(val => ({
      ...val,
      ...video
    }))
    hideMenu()
  }

  async function handleLogout() {
    const response = await fetch(LOGOUT(accessToken))
    const json = await response.json()
    if(json){ 
      updateAccessToken()
      hideLogoutMenu()
      localStorage.removeItem('accessToken')
      window.location.search = ''
    }
    return
  }

  function openMeetLink() {
    if(meetingLink) window.open(meetingLink)
    else window.alert('Meeting link unavailable')
  }

  return <>
    <Sidebar.Pushable>
      <Sidebar
        as={Menu}
        animation='overlay'
        icon='labeled'
        inverted
        onHide={hideMenu}
        vertical
        visible={visible}
      >
        {liveData && liveData.videoUrl ? <>
          <Menu.Item>
            <Header inverted as='b' className="red">LIVE</Header>
          </Menu.Item>
          <Menu.Item name="video" onClick={() => updateCurrentVideo(liveData)}>
            <Menu.Header as='a'>{liveData.name}</Menu.Header>
            <p>{liveData.description}</p>
          </Menu.Item>
        </> : null}
        {vidList && Object.keys(vidList).sort().map(subject => {
          return (
            <Menu.Item key={subject}>
              <Menu.Item>
                <div>
                  <Icon name="book"/>
                  <Header inverted as='b' className="blue">{subject}</Header> &nbsp;
                  <Icon name="book"/>
                </div>
              </Menu.Item>
              {vidList[subject].map((video) => (
                <Menu.Item key={video.id} name="video" onClick={() => updateCurrentVideo(video)}>
                  <Menu.Header as='a'>{video.name}</Menu.Header>
                  <p>{video.description}</p>
                </Menu.Item>
              ))}
            </Menu.Item>
          )
        })}
      </Sidebar>
      <Sidebar.Pusher dimmed={visible} className="full-screen">
        <div className="full-screen">
          <div className="full-width">
            <Menu inverted className="scrollable-x">
              <Menu.Item
                name='list'
                onClick={showMenu}
              >
                <Icon name='list' />
              </Menu.Item>
              <Menu.Item as='b' header>
                {standard}
              </Menu.Item>
              <Menu.Item as='b' header>
                {subject}
              </Menu.Item>
              <Menu.Item as='b' header>
                {name}
              </Menu.Item>
              <Menu.Item icon="video camera" position="right" onClick={openMeetLink} header/>
              <Menu.Item icon="log out" onClick={showLogoutMenu} header />
            </Menu>
          </div>
          <div className="full-screen">
            {isEligibility && videoUrl ? (
              <div className="iframe-full-screen">
                <iframe 
                  frameBorder="0"
                  title="Live class" 
                  width="100%" 
                  height="100%" 
                  src={videoUrl} 
                  allowFullScreen='false'
                />
              </div>
              ) : message ? (
              <Container textAlign="center" inverted>
                <Message>{message}</Message>
              </Container>
            ) : null}
          </div>
        </div>
        <Modal
          onClose={hideLogoutMenu}
          open={showLogout}
        >
          <Modal.Header>Help</Modal.Header>
          <Modal.Content image>
            <Modal.Description>
              Logout from this device, to view classes from another device
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={hideLogoutMenu}>
              No, I want to watch live class here.
            </Button>
            <Button color='red' onClick={handleLogout} negative>
             Yes, log me out.
            </Button>
          </Modal.Actions>
        </Modal>
      </Sidebar.Pusher>
    </Sidebar.Pushable>
  </>
}

export default App;
